.products-section {
    margin-top: 40px;
    display: flex;
    align-items: flex-start;
}

.product-item__sale {
    text-decoration: line-through;
    color: rgb(169, 169, 169);
}

.product-item__price {
    display: block;
    color: var(--color-red);
    font-size: 20px;
    font-weight: 600;
    margin-top: 5px;
}

.products-content {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 350px);
    gap: 15px;
    padding: 0 15px;
    justify-items: center;
}

.product-item__name {
    font-size: 22px;
    text-align: center;
}

.product-item__amount{
    letter-spacing: 1.7px;
    color: white;
    border-radius: 9px;
    padding: 5px;
    position: absolute;
    top: 15px;
    left: 15px;
    font-size: 19px;
    font-weight: 600;
    background-color: var(--color-red);
}

.product-item__prices {
    text-align: right;
    padding-right: 20px;
    padding-bottom: 20px;
}

.product-item {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    width: 100%;
    padding: 10px;
    transition: .1s;
    border-radius: 9px;
    position: relative;
    aspect-ratio: 1;
    background-color: #fff;
}

.product-item a{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.product-item:hover {
    box-shadow: 0px 0px 15px rgba(196, 61, 61, 0.15);
    width: 100%;
    padding: 10px;
}

.product-item__content {
    margin-top: 10px;
    color: black;
}



.product-item__image {
    text-align: center;
    width: 100%;
    margin-top: 50px;
}

.product-item__image img {
    width: 150px;
    height: 150px;
}

.products-aside {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 9px;
    background-color: #fff;
}

.products-aside input {
    display: block;
    margin: 0 auto 10px;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 9px 9px 0 0;
    border-bottom: 1px dashed #000;
}

.products-aside-category {
    margin-top: 20px;
}

.products-aside-category__title {
    padding: 5px 10px;
}

.products-aside-category__buttons button {
    width: 100%;
    border: none;
    background-color: transparent;
    text-align: left;
    padding: 10px;
    transition: .1s;
    font-weight: 500;
}

.products-aside-category__buttons button:hover {
    background-color: var(--color-gray);
}

.products-aside-category__button--active {
    background-color: var(--color-red) !important;
    color: white;
}