.cart-item{
    box-shadow: 0px 0px 15px rgba(0,0,0,0.1);
    margin-bottom: 15px;
    border-radius: 9px;
    padding: 10px;
    position: relative;
    background-color: #fff;
}

.cart-product__buttons{
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
}

.cart-content{
    display: flex;
    align-items: flex-start;
}

.cart-list{
    width: 100%;
    margin-right: 20px;
}

.cart-aside{
    background-color: #fff;
    box-shadow: 0px 0px 15px rgba(0,0,0,0.1);
    padding: 10px;
    border-radius: 9px;
}

.cart-total{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart-product__name{
    margin-left: 15px;
}

.cart-product{
    display: flex;
    align-items: center;
}
.cart-product button{
    background-color:transparent;
    border: none;
}
.cart-product__image{
    width: 45px;
    height: 45px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.cart-buttons button{
    background-color: var(--color-red);
    width: 100%;
    padding: 10px;
    font-size: 16px;
    font-weight: 600;
    border: none;
    color: white;
    border-radius: 2px;
    margin-top: 10px;
}

.cart-buttons button:disabled{
    background-color: rgb(194, 194, 194);
    cursor:not-allowed;
}

.cart-input{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 10px 0;
}
.cart-input label{
    margin-bottom: 5px;
}
.cart-input input{
    border: 1px solid var(--color-red);
    padding: 5px 15px;
    font-size: 17px;
    border-radius: 2px;
}
.cart-user h2{
    margin-bottom: 20px;
}
.cart-user{
    text-align: center;
    border-bottom: 1px dashed #000;
    padding: 20px 0;
}