:root {
    --font-title: 'Staatliches', cursive;
    --font-p: 'Hind Madurai', sans-serif;
    --color-red: rgb(196, 61, 61);
    --color-gray: rgb(236, 236, 236);
}

body {
    font-family: var(--font-p);
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

button {
    cursor: pointer;
}

.page {
    margin-top: 40px;
}

aside {
    width: 350px;
}

.list-message {
    width: 100%;
    text-align: center;
}

.background {
    z-index: -1;
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100vh;
    background-position: 0%;
    background-size: 130%;
    background-repeat: no-repeat;
    opacity: .7;
    filter: blur(1px);
}

.notification {
    color: white;
    position: absolute;
    background-color: rgba(196, 61, 61, .9);
    padding: 5px 30px;
    border-radius: 5px;
    top: 30px;
    right: 50%;
    transform: translateX(50%);
    text-align: center;
    width: 400px;
    transition: all .2s ease-out;
}

.notification button {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    background: transparent;
    border: none;
    color: white;
}

.notification-inactive {
    opacity: 0;
    transform: translate(50%, -100%);
}

.notification-active {
    opacity: 1;
    transform: translate(50%, 0);
}

.modal {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.3);
}

.modal-wrapper{
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%,-50%);
}