.product__image {
    width: 500px;
    height: 350px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 90%;
    border: 1px dashed var(--color-red);
    border-radius: 9px;
}

.product-content {
    display: flex;
    align-items: flex-start;
}

.product__head {
    display: flex;
    align-items: stretch;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 9px;
    width: 100%;
    background-color: #fff;
}

.product__count button {
    background-color: var(--color-gray);
    padding: 7px 20px;
    border: none;
    font-size: 16;
    font-weight: 600;
    border-radius: 2px;
}

.product__info input {
    background-color: var(--color-gray);
    padding: 7px 5px;
    border: none;
    border-radius: 2px;
    text-align: center;
    width: 55%;
}

.product__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px dashed #000;
}

.product__info {
    background-color: #fff;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    margin-left: 20px;
    position: relative;
    width: 425px;
    border-radius: 9px;
    padding: 20px;
}

.product__count {
    margin: 20px 0;
    text-align: center;
    display: flex;
    justify-content: space-between;
}

.product__cart {
    border: 1px solid var(--color-red);
    background-color: var(--color-red);
    color: white;
    width: 100%;
    font-size: 16;
    font-weight: 600;
    border-radius: 2px;
    padding: 10px;
}

.product__cart:active{
    background-color: var(--color-red) !important;
    color: white;
}
.product__cart--active{
    color: var(--color-red);
    background-color: white !important;
}

.product__desc {
    padding: 20px;
    width: 100%;
    position: relative;
}

.product__desc h3 {
    margin-top: 20px;
    margin-bottom: 10px;
}

.product__desc p {
    text-align: justify;
}

.product__time{
    position: absolute;
    bottom: 20px;
    color: var(--color-red);
}